import _ from 'lodash'
import { RARITY_STRENGTH } from './cards.js'

const RESOURCE_CHAIN = {
  'Solar Panel Array': { power: 1 },
  'Fission Reactor': { power: 2 },
  'Megapower Reactor': { power: 3 },
  'Ice Mining Rig': { power: -1, water: 2 },
  'Inflatable Sleeping Pod': { power: -1, wellbeing: 2 },
  'Rover': { martia: 2, power: -1 },
  'Scouting Drone': { martia: 2, power: -1 },
  'Buggy': { martia: 2, power: -1 },
  'Fuel Plant': { power: -1, water: -2, oxygen: 1, fuel: 1 },
  '3D Printing Robot': { power: -1, regolith: -2, construction_materials: 2 },
  'Inflatable Greenhouse': { power: -1, water: -2, food: 1 },
  'Male Engineer': { water: -1, wellbeing: -2, food: -1, oxygen: -1, engineer_labor: 3 },
  'Female Engineer': { water: -1, wellbeing: -2, food: -1, oxygen: -1, engineer_labor: 3 },
  'Male Scientist': { water: -1, wellbeing: -2, food: -1, oxygen: -1, science_labor: 3 },
  'Female Scientist': { water: -1, wellbeing: -2, food: -1, oxygen: -1, science_labor: 3 },
  'Male Botanist': { water: -1, wellbeing: -2, food: 0, oxygen: -1, science_labor: 3 },
  'Female Botanist': { water: -1, wellbeing: -2, food: 0, oxygen: -1, science_labor: 3 },
  'Male Doctor': { water: -1, wellbeing: -2, food: -1, oxygen: -1, social_labor: 2, medicine: 1 },
  'Female Doctor': { water: -1, wellbeing: -2, food: -1, oxygen: -1, social_labor: 2, medicine: 1 },
  'Inflatable Laboratory': { power: -2, science_labor: -2, data: 3 },
  'Fuel Generator': { power: -2, water: -3, oxygen: 1, fuel: 2, engineer_labor: -1 },
  'Inflatable Dining Hall': { power: -2, wellbeing: 2, food: -2, social_labor: -1 },
  'Inflatable Social Hub': { power: -2, water: -3, wellbeing: 2, social_labor: -1 },
  'Medical Bay': { power: -2, social_labor: -1, medicine: 3 },
  'Communication Array': { power: -3, science_labor: -1, data: 3 },
  'Cargo Rocketship': { construction_materials: 2, engineer_labor: -2, logistic_labor: -2, repairs: 2 },
  'Crew Rocketship': { construction_materials: 2, engineer_labor: -2, logistic_labor: -2, medicine: 2 },
}

class Resources {
  constructor(values = {}) { this.values = values }
  getValues() { return this.values }
  getValue(resName) { return _.get(this.values, resName, 0.0) }
  add(resources) {
    let changedValues = {}
    for (const [key, value] of _.entries(resources.getValues())) {
      changedValues[key] = this.getValue(key) + value
    }
    return new Resources({ ...this.values, ...changedValues })
  }
  sub(resources) {
    let changedValues = {}
    for (const [key, value] of _.entries(resources.getValues())) {
      changedValues[key] = this.getValue(key) - value
    }
    return new Resources({ ...this.values, ...changedValues })
  }
  set(resources) {
    for (const [key, value] of _.entries(resources.getValues())) {
      this.values[key] = value
    }
    return this
  }
}

const cardInput = ({ name, rarity }) => {
  const cardResources = RESOURCE_CHAIN[name]
  const strength = RARITY_STRENGTH[rarity]

  const values = _.entries(cardResources)
    .filter(([resource, quantity]) => quantity < 0.0)
    .reduce((acc, [resource, quantity]) => ({ ...acc, [resource]: -quantity * strength }), {})
  return new Resources(values)
}

const cardOutput = ({ name, rarity }) => {
  const cardResources = RESOURCE_CHAIN[name]
  const strength = RARITY_STRENGTH[rarity]
  const values = _.entries(cardResources)
    .filter(([resource, quantity]) => quantity > 0.0)
    .reduce((acc, [resource, quantity]) => ({ ...acc, [resource]: quantity * strength }), {})
  return new Resources(values)
}

export const performSimulation = (cards) => {
  console.time('sort_cards')
  const sortedCards = _.orderBy(cards, [
    (card) => _.keys(RESOURCE_CHAIN).indexOf(card.name),
    (card) => card.strength,
  ], ['asc', 'desc'])
  console.timeEnd('sort_cards')

  let balance = new Resources()

  console.time('simulation')
  const processedCards = sortedCards.map(card => {
    const requiredResources = cardInput({ name: card.name, rarity: card.rarity })
    const producedResources = cardOutput({ name: card.name, rarity: card.rarity })

    let used = false

    if (_.every(_.entries(requiredResources.getValues()), ([resource, quantity]) => balance.getValue(resource) >= quantity)) {
      used = true

      balance = balance.sub(requiredResources)
      balance = balance.add(producedResources)
    }

    return {
      ...card,
      used,
      requiredResources,
      producedResources,
      currentResources: balance
    }
  })

  console.timeEnd('simulation')

  const groupedCardsByNameAndRarity = _.mapValues(_.groupBy(processedCards, 'name'), groups => _.groupBy(groups, 'rarity'))

  const groupedCards = _.flatten(_.keys(RESOURCE_CHAIN).map(cardName => {
    return _.compact(_.keys(RARITY_STRENGTH).map(cardRarity => {
      const cards = _.get(groupedCardsByNameAndRarity, [cardName, cardRarity])

      if (!cards) return null

      const groupSummary = {
        name: cardName,
        rarity: cardRarity,
        quantity: cards.length,
        used: 0,
        requiredResources: new Resources(),
        usedResources: new Resources(),
        producedResources: new Resources(),
        potentialProducedResources: new Resources(),
        currentResources: new Resources()
      }

      for (let card of cards) {
        groupSummary.used += card.used ? 1 : 0
        groupSummary.requiredResources = groupSummary.requiredResources.add(card.requiredResources)
        groupSummary.potentialProducedResources = groupSummary.potentialProducedResources.add(card.producedResources)

        if (card.used) {
          groupSummary.usedResources = groupSummary.usedResources.add(card.requiredResources)
          groupSummary.producedResources = groupSummary.producedResources.add(card.producedResources)
        }
        groupSummary.currentResources = card.currentResources
      }

      return groupSummary
    }))
  }))

  const usedStrength = processedCards.filter(card => card.used).reduce((sum, card) => sum + card.strength, 0)
  const totalStrength = processedCards.reduce((sum, card) => sum + card.strength, 0)
  const score = usedStrength / totalStrength

  return {
    cards: processedCards,
    groupedCards,
    balance,
    usedStrength,
    totalStrength,
    score
  }
}
